.container {
    min-height: 100vh;
    padding: 0 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}

.main {
    padding: 5rem 0;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer {
    width: 100%;
    border-top: 1px solid #eaeaea;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer a {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
}

.title a {
    color: #0070f3;
    text-decoration: none;
}

.title a:hover,
.title a:focus,
.title a:active {
    text-decoration: underline;
}

.title {
    margin: 0;
    line-height: 1.15;
    font-size: 4rem;
}

.title,
.description {
    text-align: center;
}

.description {
    line-height: 1.5;
    font-size: 1.5rem;
}

.code {
    background: #fafafa;
    border-radius: 5px;
    padding: 0.75rem;
    font-size: 1.1rem;
    font-family: Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono,
    Bitstream Vera Sans Mono, Courier New, monospace;
}

.grid {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 10vh auto;
    gap: 16px;
    justify-content: center;
}

.grid .card {
    background-color: var(--bg-color);
    text-align: left;
    color: inherit;
    text-decoration: none;
    border: 1px solid #eaeaea;
    border-radius: 10px;
    transition: color 0.35s ease, font-size .25s ease-in;
    inset: 16px;
    height: 140px;
    font-size: 20px;
    font-weight: bold;
    min-width: 200px;
    position: relative;
}

.card .card-content {
    border-radius: inherit;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    inset: 24px;
    padding: 10px;
    position: absolute;
    z-index: 2;
    justify-content: flex-end;
}

.card::before,
.card::after {
    border-radius: inherit;
    content: "";
    height: 100%;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    transition: opacity 500ms;
    width: 100%;
}

.card:before {
    background: radial-gradient(
        800px circle at var(--mouse-x) var(--mouse-y),
        rgba(127, 127, 255, 0.31),
        transparent 40%
    );
    z-index: 3;
}

.card::after {
    background: radial-gradient(
        400px circle at var(--mouse-x) var(--mouse-y),
        rgba(255, 0, 0, 0.8),
        transparent 40%
    );
    z-index: 1;
}

.card:hover:before {
    opacity: 1;
}

.cards:hover > .card:after {
    opacity: 1;
}

.card.small {
    width: calc(26% - 64px);
}

.card.skill {
    height: 50px;
    width: calc(15vw - 64px);
}

.card:hover,
.card:focus,
.card:active {
    color: #0070f3;
    border-color: #0070f3;
    opacity: 1;
}


.card.showcase:hover,
.card.showcase:focus {
    font-size: 26px;
    color: white;
    border-color: #0070f3;
}

.card.showcase:active {
    color: red;
    border-color: white;
}

.card.skill:hover,
.card.skill:focus,
.card.skill:active {
    color: orange;
    border-color: orangered;
}

.card h2 {
    margin: 0 0 1rem 0;
    font-size: 1.5rem;
}

.card p {
    margin: 0;
    font-size: 1.25rem;
    line-height: 1.5;
}

.logo {
    height: 1em;
    margin-left: 0.5rem;
}

@media (max-width: 600px) {
    .grid {
        width: 100%;
        flex-direction: column;
    }

    .card.small {
        width: 45%;
    }
}

